<!-- 练习学习 -->
<template>
  <div class="Practice">
    <el-backtop> </el-backtop>
    <p class="ajax">AJAX</p>

    <p class="list">1. AJAX 实例</p>
    <div class="sec">
      <div>
        <h3>{{msg}}</h3>
      </div>
      <el-button type="button" v-on:click="loadXMLDoc">Change Content</el-button>
    </div>
    <!-- 分界线 -->
    <p class="list">2. AJAX XHR</p>
    <div class="sec">
      <h4>XMLHttpRequest 对象用于和服务器交换数据。</h4>
      <h4>
        GET 请求 xmlhttp.open("GET","demo_get.asp",true);
        xmlhttp.send();
      </h4>
      <h4>
        POST 请求 xmlhttp.open("POST","demo_get.asp",true);
        xmlhttp.send();
      </h4>
    </div>
    <!-- 分界线 -->
    <div class="sec">
      <div id="myDiv"></div>
      <el-button type="button" v-on:click="loadXMLDoc1">请求数据</el-button>
    </div>
    <!-- 分界线 -->
    <p class="list">3. 服务器响应</p>
    <div class="sec">
      <h4>如需获得来自服务器的响应，请使用 XMLHttpRequest 对象的 responseText 或 responseXML 属性。</h4>
    </div>
    <!-- 分界线 -->
    <p class="list">4. AJAX ASP/PHP 请求实例</p>
    <div class="sec">
      <h3>请在下面的输入框中键入字母（A - Z）：</h3>姓氏：
      <input v-model="input" type="text" />

      <p>
        建议：
        <span>{{input}}</span>
      </p>
    </div>
    <!-- 分界线 -->
    <p class="list">5. 读文件</p>
    <div class="sec">
       
    </div>
    <!-- 分界线 -->
    <p class="list">6. 读文件</p>
    <div class="sec">
     
    </div>
    <!-- 分界线 -->
    <p class="list">7. 读文件</p>
     
  </div>
</template>

<script>
 
 
 
export default {
  data() {
    //选项 / 数据
    return {
      msg: "Let AJAX change this text",
      input: "",
  
    };
  },
  methods: {
    loadXMLDoc: function() {
      this.msg = "AJAX is not a programming language. ";
      this.msg +=
        " It is just a technique for creating better and more interactive web applications.";
    },
    loadXMLDoc1: function() {
      var xmlhttp;
      if (window.XMLHttpRequest) {
        // code for IE7+, Firefox, Chrome, Opera, Safari
        xmlhttp = new XMLHttpRequest();
      } else {
        // code for IE6, IE5
        xmlhttp = new ActiveXObject("Microsoft.XMLHTTP");
      }
      xmlhttp.onreadystatechange = function() {
        if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
          document.getElementById("myDiv").innerHTML = xmlhttp.responseText;
        }
      };
      var fname = "/static/article/fujie.txt?t=" + Math.random();
      xmlhttp.open("GET", fname, true);
      xmlhttp.send();
      //   alert(fname);
    },
    showHint: function() {
      var str = this.input;
      var xmlhttp;
      if (str.length == 0) {
        document.getElementById("txtHint").innerHTML = "";
        return;
      }
      if (window.XMLHttpRequest) {
        // code for IE7+, Firefox, Chrome, Opera, Safari
        xmlhttp = new XMLHttpRequest();
      } else {
        // code for IE6, IE5
        xmlhttp = new ActiveXObject("Microsoft.XMLHTTP");
      }
      xmlhttp.onreadystatechange = function() {
        if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
          document.getElementById("txtHint").innerHTML = xmlhttp.responseText;
        }
      };
      xmlhttp.open("GET", "/static/article/gethint.asp?q=" + str, true);
      xmlhttp.send();
    },
    
  },
  components: {
     
  },
  created() {},
  computed: {},
  mounted() {
 
  }
};
</script>

<style>
.Practice {
  margin-top: -80px;
  /* position: relative; */
  width: 75%;
  margin-left: 13%;
  margin-bottom: 40px;
}

.ajax {
  text-align: center;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 30px;
  font-weight: bold;
}
.list {
  text-align: left;
  padding: 2px 2px;
  font-size: 15px;
  font-weight: bold;
}
.sec {
  /* height: 15px; */
  margin-bottom: 10px;
  text-align: center;
  border-radius: 15px;
  background: rgb(131, 199, 245, 0.8);
  padding: 6px 6px;
}
h4 {
  text-align: left;
}
</style>


